/*Images and Overlays
---------------------------------- */
.overlay-container {
  position: relative;
  display: block;
  text-align: center;
  overflow: hidden;
}
.overlay-bottom,
.overlay-top {
  position: absolute;
  top: auto;
  padding: 15px;
  bottom: 0px;
  left: 0;
  right: 0px;
  background-color: rgba(30, 30, 30, 0.5);
  overflow: hidden;
  @include opacity(0);
  @include transition(all ease-in-out 0.25s);
  height: 0;
  color: $white;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}
.overlay-bottom.links,
.overlay-to-top.links {
  z-index: 11;
}
.overlay-to-top {
  position: absolute;
  top: auto;
  padding: 10px;
  bottom: -30px;
  left: 0;
  right: 0px;
  background-color: rgba(30, 30, 30, 0.5);
  @include opacity(0);
  @include transition(all ease-in-out 0.25s);
  color: $white;
}
.overlay-top {
  top: 0;
}
.overlay-visible .overlay-bottom,
.overlay-visible .overlay-top {
  @include opacity(1);
  height: auto!important;
  padding-bottom: 20px;
}
.overlay-visible.overlay-container:hover .overlay-bottom:not(.links),
.overlay-visible.overlay-container:hover .overlay-top {
  @include opacity(0.5);
}
.overlay-top a,
.overlay-bottom a,
.overlay-to-top a,
.overlay-top a:hover,
.overlay-bottom a:hover,
.overlay-to-top a:hover {
  color: $white;
  text-decoration: none;
}
.overlay-container .text {
  padding: 0 20px;
  position: relative;
}
.overlay-container .text h3,
.overlay-container .text h3 .small {
  margin-top: 0;
  color: $white;
}
.overlay-container .text a:hover {
  color: $white;
}
.overlay-bottom .btn,
.overlay-top .btn {
  color: $white;
  border-color: $white;
  margin: 0;
}
.overlay-bottom .btn:hover,
.overlay-top .btn:hover {
  color: $gray;
  background-color: $white;
}
.overlay-container:hover .overlay-top,
.overlay-container:hover .overlay-bottom,
.image-box.style-2:hover .overlay-top,
.image-box.style-2:hover .overlay-bottom {
  height: 50%;
  @include opacity(1);
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}
.overlay-container:hover .overlay-to-top,
.image-box:hover .overlay-to-top {
  @include opacity(1);
  bottom: 0;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}
.overlay-link {
  position: absolute;
  z-index: 10;
  top: 0;
  padding: 15px;
  bottom: 0px;
  left: 0;
  right: 0px;
  background-color: rgba(30, 30, 30, 0.5);
  overflow: hidden;
  @include opacity(0);
  @include transition(all ease-in-out 0.25s);
  color: $white;
}
.overlay-link i {
  @include transition(all 0.2s ease-in-out);
  width: 80px;
  height: 80px;
  border: 1px solid $white;
  @include border-radius(100%);
  color: $white;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -40px 0 0 -40px;
  font-size: 32px;
  line-height: 80px;
}
.overlay-link i:hover {
  background-color: $white;
  color: $gray;
}
.overlay-link.small i {
  width: 25px;
  height: 25px;
  margin: -12.5px 0 0 -12.5px;
  font-size: 14px;
  line-height: 25px;
}
.overlay-container:hover .overlay-link {
  height: 100%;
  @include opacity(1);
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

/* Large devices (Large desktops 1200px and up) */
@media (min-width: 1200px) {
  .overlay-container .text {
    top: 30%;
  }
}

/* Extra small devices (phones, less than 575px) */
@media (max-width: 575px) {
  .overlay-container .text {
    top: 30%;
  }
  .overlay-container .text p:not(.mobile-visible) {
    display: none;
  }
}

/* Large and Medium devices (desktops, tablets less than 1199px) */
@media (min-width: 575px) and (max-width: 1199px) {
  .overlay-container:not(.overlay-visible) .overlay-top ~ .overlay-bottom {
    padding-top: 5px;
    padding-bottom: 5px;
  }
  .slick-slide .overlay-container:hover .overlay-top {
    height: 71%;
  }
  .slick-slide .overlay-container:hover .overlay-bottom {
    height: 29%;
  }
}

/*Video Backgrounds
---------------------------------- */
.video-background,
.video-background-banner {
  position: relative;
  overflow: hidden;
}
.video-background video,
.video-background-banner video {
  min-width: 100%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-attachment: scroll;
  min-height: 100%;
  height: auto;
  width: auto;
  top: 0%;
  left: 0%;
  background-size: cover;
  display: none;
  position: absolute;
  display: block;
}
.video-background-banner .video-background-poster,
.video-background  .video-background-poster {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-attachment: scroll;
  background-size: cover;
  z-index: 0;
}

/* Responsive iframe
---------------------------------- */
.tp-caption .embed-responsive {
  overflow: visible;
}

/* Audio wrapper
---------------------------------- */
.audio-wrapper iframe {
  width: 100%;
  border: none;
  margin-bottom: 20px;
}

/* Jumbotron
---------------------------------- */
.jumbotron {
  background-color: transparent;
}

/*Carousel
---------------------------------- */
// RTL support
// $text-direction variable is setted to "ltr" inside style.scss file and to "rtl" inside rtl_style.scss file.
@if $text-direction == rtl {
  .carousel-indicators {
    padding-right: 0;
  }
}
.top.carousel-indicators {
  bottom: auto;
  top: 10px;
}
.bottom.carousel-indicators {
  bottom: 10px;
}
.shadow .carousel-indicators {
  position: absolute;
  z-index: 4;
}

/*Badges
---------------------------------- */
.main-navigation .badge {
  position: absolute;
  top: 13px;
  // RTL support.
  // The following mixin depends on the $text-direction variable in style.scss file OR in rtl_style.scss file.
  // RTL related mixins are located in mixins/_rtl.scss file.
  @include right(20px);
  font-size: 12px;
  background-color: transparent;
  color: inherit;
  font-weight: 300;
  padding: 2px 4px;
}
.main-navigation .mega-menu .badge {
  top: 8px;
  // RTL support
  // $text-direction variable is setted to "ltr" inside style.scss file and to "rtl" inside rtl_style.scss file.
  @if $text-direction == rtl {
    left: 0;
  }
}

/* Extra small devices (phones, less than 767px) */
@media (max-width: 767px) {
  .main-navigation .dropdown-toggle .badge {
    // RTL support.
    // The following mixin depends on the $text-direction variable in style.scss file OR in rtl_style.scss file.
    // RTL related mixins are located in mixins/_rtl.scss file.
    @include right(50px);
  }
}

/*Pace (Page loader)
---------------------------------- */
/*loader 1*/
.page-loader-1 {
  .page-wrapper {
    @include opacity(0);
  }
  &.pace-done .page-wrapper {
    @include opacity(1);
    @include transition(ease-in-out opacity 0.2s);
  }
  .pace {
    -webkit-pointer-events: none;
    pointer-events: none;
    @include user-select(none);
    z-index: 2000;
    position: fixed;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 5px;
    width: 200px;
    background: $white;
    overflow: hidden;
  }
  .pace .pace-progress {
    @include box-sizing(border-box);
    @include translate3d(0, 0, 0);
    max-width: 200px;
    position: fixed;
    z-index: 2000;
    display: block;
    position: absolute;
    top: 0;
    right: 100%;
    height: 100%;
    width: 100%;
  }
}

/*loader 2*/
.page-loader-2 {
  .page-wrapper {
    @include opacity(0);
  }
  &.pace-done .page-wrapper {
    @include opacity(1);
    @include transition(ease-in-out opacity 0.2s);
  }
  .pace {
    -webkit-pointer-events: none;
    pointer-events: none;
    @include user-select(none);
    z-index: 2000;
    position: fixed;
    height: 60px;
    width: 100px;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .pace .pace-progress {
    z-index: 2000;
    position: absolute;
    height: 60px;
    width: 100px;
    -webkit-transform: translate3d(0, 0, 0) !important;
    -ms-transform: translate3d(0, 0, 0) !important;
    transform: translate3d(0, 0, 0) !important;
  }
  .pace .pace-progress:before {
    content: attr(data-progress-text);
    text-align: center;
    color: $white;
    border-radius: 50%;
    font-family: "Helvetica Neue", sans-serif;
    font-size: 14px;
    font-weight: 100;
    line-height: 45px;
    padding: 0;
    width: 44px;
    height: 44px;
    margin: 8px 0 0 29px;
    display: block;
    z-index: 999;
    position: absolute;
  }
  .pace .pace-activity {
    font-size: 15px;
    line-height: 1;
    z-index: 2000;
    position: absolute;
    height: 60px;
    width: 100px;
    display: block;
    @include animation(pace-theme-center-atom-spin 2s linear infinite);
  }
  .pace .pace-activity {
    border-radius: 50%;
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 60px;
    width: 100px;
  }
  .pace .pace-activity:after {
    border-radius: 50%;
    content: ' ';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    height: 60px;
    width: 100px;
    @include rotate(60deg);
  }
  .pace .pace-activity:before {
    border-radius: 50%;
    content: ' ';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    height: 60px;
    width: 100px;
    @include rotate(120deg);
  }
}
@-webkit-keyframes pace-theme-center-atom-spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
  }
}

@-moz-keyframes pace-theme-center-atom-spin {
  0% {
    -moz-transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(359deg);
  }
}

@-o-keyframes pace-theme-center-atom-spin {
  0% {
    -o-transform: rotate(0deg);
  }
  100% {
    -o-transform: rotate(359deg);
  }
}
@keyframes pace-theme-center-atom-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

/*loader 3*/
.page-loader-3 {
  .pace {
    -webkit-pointer-events: none;
    pointer-events: none;
    @include user-select(none);
  }
  .pace .pace-progress {
    position: fixed;
    z-index: 2000;
    top: 0;
    right: 100%;
    width: 100%;
    height: 2px;
  }
}
.pace.pace-inactive {
  display: none;
}

/*loader 4*/
.page-loader-4 {
  .page-wrapper {
    @include opacity(0);
  }
  &.pace-done .page-wrapper {
    @include opacity(1);
    @include transition(ease-in-out opacity 0.2s);
  }
  .pace {
    -webkit-pointer-events: none;
    pointer-events: none;
    @include user-select(none);
    @include perspective(12rem);
    z-index: 2000;
    position: fixed;
    height: 6rem;
    width: 6rem;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .pace .pace-progress {
    position: fixed;
    z-index: 2000;
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 6rem;
    width: 6rem !important;
    line-height: 6rem;
    font-size: 2rem;
    border-radius: 50%;
    color: $white;
    font-family: "Helvetica Neue", sans-serif;
    font-weight: 100;
    text-align: center;
    @include animation(pace-theme-center-circle-spin linear infinite 2s);
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -ms-transform-style: preserve-3d;
    -o-transform-style: preserve-3d;
    transform-style: preserve-3d;
  }
  .pace .pace-progress:after {
    content: attr(data-progress-text);
    display: block;
  }
}
@-webkit-keyframes pace-theme-center-circle-spin {
  from {
    -webkit-transform: rotateY(0deg);
  }
  to {
    -webkit-transform: rotateY(360deg);
  }
}
@-moz-keyframes pace-theme-center-circle-spin {
  from {
    -moz-transform: rotateY(0deg);
  }
  to {
    -moz-transform: rotateY(360deg);
  }
}
@-ms-keyframes pace-theme-center-circle-spin {
  from {
    -ms-transform: rotateY(0deg);
  }
  to {
    -ms-transform: rotateY(360deg);
  }
}
@-o-keyframes pace-theme-center-circle-spin {
  from {
    -o-transform: rotateY(0deg);
  }
  to {
    -o-transform: rotateY(360deg);
  }
}
@keyframes pace-theme-center-circle-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

/*loader 5*/
.page-loader-5 {
  .page-wrapper {
    @include opacity(0);
  }
  &.pace-done .page-wrapper {
    @include opacity(1);
    @include transition(ease-in-out opacity 0.2s);
  }
  .pace {
    -webkit-pointer-events: none;
    pointer-events: none;
    @include user-select(none);
  }
  .pace .pace-progress {
    position: fixed;
    z-index: 2000;
    top: 0;
    right: 0;
    height: 5rem;
    width: 5rem;
    -webkit-transform: translate3d(0, 0, 0) !important;
    -ms-transform: translate3d(0, 0, 0) !important;
    transform: translate3d(0, 0, 0) !important;
  }
  .pace .pace-progress:after {
    display: block;
    position: absolute;
    top: 0;
    right: .5rem;
    content: attr(data-progress-text);
    font-family: "Helvetica Neue", sans-serif;
    font-weight: 100;
    font-size: 100px;
    line-height: 1;
    text-align: right;
  }
}

/*loader 6*/
.page-loader-6 {
  .page-wrapper {
    @include opacity(0);
  }
  &.pace-done .page-wrapper {
    @include opacity(1);
    @include transition(ease-in-out opacity 0.2s);
  }
  .pace {
    -webkit-pointer-events: none;
    pointer-events: none;
    @include user-select(none);
  }
  .pace .pace-activity {
    display: block;
    position: fixed;
    z-index: 2000;
    top: 0;
    right: 0;
    width: 300px;
    height: 300px;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    -webkit-transform: translateX(100%) translateY(-100%) rotate(45deg);
    transform: translateX(100%) translateY(-100%) rotate(45deg);
    pointer-events: none;
  }
  .pace.pace-active .pace-activity {
    -webkit-transform: translateX(50%) translateY(-50%) rotate(45deg);
    transform: translateX(50%) translateY(-50%) rotate(45deg);
  }
  .pace .pace-activity::before,
  .pace .pace-activity::after {
    @include box-sizing(border-box);
    position: absolute;
    bottom: 30px;
    left: 50%;
    display: block;
    border: 5px solid $white;
    border-radius: 50%;
    content: '';
  }
  .pace .pace-activity::before {
    margin-left: -40px;
    width: 80px;
    height: 80px;
    border-right-color: rgba($black, 0.2);
    border-left-color: rgba($black, 0.2);
    @include animation(pace-theme-corner-indicator-spin 3s linear infinite);
  }
  .pace .pace-activity::after {
    bottom: 50px;
    margin-left: -20px;
    width: 40px;
    height: 40px;
    border-top-color: rgba($black, 0.2);
    border-bottom-color: rgba($black, 0.2);
    @include animation(pace-theme-corner-indicator-spin 1s linear infinite);
  }
}
@-webkit-keyframes pace-theme-corner-indicator-spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
  }
}
@keyframes pace-theme-corner-indicator-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

